.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.color span {
  display: inline-block;
  margin-right: 5px;
  width: 20px;
  height: 20px;
  border: 1px solid #000;
  cursor: pointer;
}

.color span.active {
  border: 3px solid #000;
}

.img img {
  width: 100%;
  height: auto;
}

.resul {
  padding: 20px;
  border: 1px solid #ccc;
}

.customers {
  text-align: left;
  width: 100%
}

.customers tr {
  height: 35px;
}